<template>
  <div id="app">
    <div class="row">
      <div class="col-lg-12 col-xl-6 ml-0 py-4">
        <!-- HEADER -->
        <Header />

        <!-- RESULT -->
        <hr class="mt-4">
        <div id="result" class="text-center">
          <p>If an election were held today,{{ winner == 'COALITION' ? ' the' : '' }}</p>
          <h2 id="winner"><span :style="{ backgroundColor: winnerColor }">{{ winner }}</span></h2>
          <p>
            would{{ winner == 'NO-ONE' ? '' : ' likely' }} form a
            <b v-html="majority ? 'majority' : 'minority'"></b> government.
          </p>
        </div>
        <hr>

        <!-- SWINGS (big) -->
        <Swings class="big-swings" />
        <p class="w-100 text-center text-muted">updated {{ time }}</p>
      </div>

      <div class="col-md">
        <ul id="selector" class="pagination pagination-sm d-flex align-items-center">
          &rarr;
          <li class="page-item ml-2" :class="{ active: activeTab == 'chamber' }">
            <a class="page-link" data-target="#toggle-chamber" v-on:click="toggleChamber">Chamber</a>
          </li>
          <li class="page-item small-swings" :class="{ active: activeTab == 'swings' }">
            <a class="page-link" data-target="#toggle-swings" v-on:click="toggleSwings">Swings</a>
          </li>
          <li class="page-item mr-2" :class="{ active: activeTab == 'pendulum' }">
            <a class="page-link" data-target="#toggle-pendulum" v-on:click="togglePendulum">Pendulum</a>
          </li>
          &larr;
        </ul>

        <!-- CHAMBER -->
        <Chamber id="toggle-chamber" :colors="colors" />
        <!-- SWINGS (small) -->
        <Swings id="toggle-swings" class="small-swings" />
        <!-- PENDULUM -->
        <Pendulum id="toggle-pendulum" class="overflow-auto mx-auto" :colors="colors" />
      </div>
    </div>
  </div>
</template>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@1,800&display=swap');

#app {
  font-family: -apple-system, 'Helvetica Neue Light', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /* height: 100vh; */
}

.row {
  padding: 0 15px;
  /* height: 100vh; */
  margin: 0px;
}

#result p {
  margin: 10px 0;
  font-size: 1.25rem;
}

#winner span {
  font-family: 'JetBrains Mono', sans-serif;
  font-weight: bolder;
  font-style: italic;
  color: #FFFFFF;
  padding: 0 10px;
}

#selector {
  justify-content: center;
}

#selector a {
  cursor: pointer;
}

@media (min-width: 1200px) {
  #selector {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  #toggle-chamber {
    padding-top: 20px;
  }

  #toggle-pendulum {
    height: 70vh;
  }

  .small-swings {
    display: none
  }
}

@media (max-width: 1199px) {
  #toggle-chamber, #toggle-swings, #toggle-pendulum {
    margin-top: 20px;
  }

  .big-swings {
    display: none
  }
}

@media (max-width: 769px) {
  #toggle-chamber {
    margin-top: 40px
  }
}

</style>

<script>
import moment from 'moment-timezone'
import $ from 'jquery'
import * as p from '../pendulum'
import Header from "@/components/Header.vue"
import Swings from "@/components/Swings.vue"
import Pendulum from "@/components/Pendulum.vue"
import Chamber from "@/components/Chamber.vue"

export default {
  name: 'App',
  components: {
    Header,
    Swings,
    Pendulum,
    Chamber
  },

  methods: {
    toggleChamber() {
      $('#toggle-chamber').slideDown()
      $('#toggle-swings').slideUp()
      $('#toggle-pendulum').slideUp()
      this.activeTab = 'chamber'
    },
    toggleSwings() {
      $('#toggle-chamber').slideUp()
      $('#toggle-swings').slideDown()
      $('#toggle-pendulum').slideUp()
      this.activeTab = 'swings'
    },
    togglePendulum() {
      $('#toggle-chamber').slideUp()
      $('#toggle-swings').slideUp()
      $('#toggle-pendulum').slideDown()
      this.activeTab = 'pendulum'
    },
    watchSwings(x) {
      if (x.matches && this.activeTab == 'swings') {
        this.toggleChamber()
      }
    }
  },

  data() {
    return {
      colors: {
        'ALP': '#F00011',
        'LP': '#080CAB',
        'NP': '#008000',
        'LNP': '#1456F1',
        'GRN': '#10C25B',
        'KAP': '#B50204',
        'CA': '#FF6300',
        'IND': '#888888'
      },
      time: '2021-04-13 23:29',
      activeTab: 'chamber',
      winner: '',
      majority: true
    }
  },
  
  created() {
    this.time = moment.tz(this.time, 'Australia/Sydney').tz(moment.tz.guess()).format('h:mma D MMM YYYY');
    
    let results = {
      'ALP': [],
      'IND': [],
      'GRN': [],
      'CA': [],
      'KAP': [],
      'NP': [],
      'LNP': [],
      'LP': []
    }

    for (let i of p['ALP']) results[i.party].push(i)
    for (let i of p['LNC']) results[i.party].push(i)

    if (results.ALP.length >= 76) {
      this.winner = 'LABOR'
      this.winnerColor = this.colors['ALP']
      this.majority = true
    } else if (results.LP.length + results.LNP.length + results.NP.length >= 76) {
      this.winner = 'COALITION'
      this.winnerColor = this.colors['LP']
      this.majority = true
    } else if (results.ALP.length > 73) {
      this.winner = 'LABOR'
      this.winnerColor = this.colors['ALP']
      this.majority = false
    } else if (results.LP.length + results.LNP.length + results.NP.length > 73) {
      this.winner = 'COALITION'
      this.winnerColor = this.colors['LP']
      this.majority = false
    } else {
      this.winner = 'NO-ONE'
      this.winnerColor = this.colors['IND']
      this.majority = true
    }
  },

  mounted() {
    $('#toggle-swings').hide()
    $('#toggle-pendulum').hide()

    let x = window.matchMedia("(min-width: 1200px)")
    this.watchSwings(x)
    x.addListener(this.watchSwings)  
  }
}
</script>
